export const useSuspense = (f, ref, ...params) => {
    if (!ref.suspender) {
        ref.suspender = f(...params)
            .then((result) => {
            console.log(result);
            ref.result = result;
        })
            .catch((e) => {
            ref.error = e;
        });
    }
    if (ref.error) {
        console.error(ref.error);
        throw ref.error;
    }
    if (ref.result) {
        return ref.result;
    }
    throw ref.suspender;
};
